    dataIndex: "name",
  },
  {
    title: "职位",
    dataIndex: "age",
  },
  {
    title: "单位",
    dataIndex: "address",
  },
  {
    title: "手机",
    dataIndex: "phone",
  },
  {
    title: "座机",
    dataIndex: "call",
  },
];

const data = [];
for (let i = 0; i < 1; i++) {
  data.push({
    key: i,
    name: `张三`,
    age: 32,
    address: `运城市`,
    phone: 1234567890,
    call: 1008611,
  });
}

export default {
